import '../public/config'
global.public = {
    server:getData
};

function getData(server_name,methods,data_str='',succ){

    const _url = global._config.GetData_url; 

    if(methods==='GET'){
        fetch(_url+server_name+"/"+data_str,{
            method: methods,
            headers: {
                "Access-Control-Allow-Origin": "*",
                "Content-Type": "text/plain"
            },
        }).then(res => res.json()).then(
            data => {
                return succ(data);
            }
        ).catch(function(err){
              throw err;
        })
    }else if(methods==='POST'){
        fetch(_url+server_name,{
            method: methods,
            headers: {
                "Access-Control-Allow-Origin": "*",
                "Content-Type": "text/plain"
            },
            body:data_str
        }).then(res => res.json()).then(
            data => {
                return succ(data);
            }
        ).catch(function(err){
            throw err;
        })
    }

}
